.about__container{
    display:grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        10deg,
        var(--color-primary),
        transparent
    );
    display: grid;
    place-items: center;
}
.about__me-image{
    border-radius: 2rem;
    transform: rotate(-8deg);
    overflow: hidden;
    transition: var(--transition);
}

.about__me-image:hover{
    transform: rotate(0);
}
.button_contact {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
}

/* <--Tablets--> */
@media  screen and (max-width: 1024px) {
    .about__container{
        grid-template-columns: 1fr;
        gap:0;

    }

    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about__content p{
        margin: 1rem 0 1.5rem;
    }
}


/* <--Mobiles--> */
@media  screen and (max-width: 600px) {
    
    .about__me{
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__cards{
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }
    .about__content{
        text-align: center;
    }
    .about_content p{
        margin: 1.5rem 0;
    }
}