@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #040304ef;
    --color-bg-variant: #2f2f2fc0;
    --color-primary: #ffffff;
    --color-primary-variant: rgba(255, 255, 255, 0.986);
    --color-white:#fff;
    --color-light: rgba(255, 255, 255, 0.6);

    --transition: all 400ms ease;
    --container-width-lg:75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}

html{
    scroll-behavior: smooth;
    height: 100%;
}

::-webkit-scrollbar{
    display:none;
}
body{
    font-family: 'Poppins', sans-serif;
    background: #040304ef;
    background: var(--color-bg);
    color: #fff;
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(data:image/png;base64,);
    height: 100%;
}


.container{
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
    height: 100%;
}

h1, h2, h3, h4, h5{
    font-weight: 500;
}
h1{
    font-size:2.5rem;
}

section{
    margin-top: 8rem;
}
section > h2, section > h5{
    text-align: center;
    color:rgba(255, 255, 255, 0.6);
    color:var(--color-light);
}

section> h2{
    color:#ffffff;
    color:var(--color-primary);
    margin-bottom: 3rem;
}


.text-light{
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
}

a{
    color:#ffffff;
    color:var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

a:hover{
    color:#ffffff;
    color:var(--color-primary);
    background: #2f2f2fc0;
    background: var(--color-bg-variant);
}

.btn{
    width: -webkit-max-content;
    width: max-content;
    display:inline-block;
    color: #ffffff;
    color: var(--color-primary);
    padding:0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #ffffff;
    border: 1px solid var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

.btn-hover{
    background : #040304ef;
    background : var(--color-bg);
    color: #040304ef;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary{
    background: #ffffff;
    background: var(--color-primary);
    color: #040304ef;
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}


/* <--Tablets--> */
@media  screen and (max-width: 1024px) {
    .container{
        width: 86%;
        width: var(--container-width-md);
    }
    section{
        margin-top: 6rem;
    }
}


/* <--Mobiles--> */
@media  screen and (max-width: 600px) {
    .container{
        width: 90%;
        width: var(--container-width-sm);
    }
    section > h2{
        margin-bottom: 2rem;
    }
}

header{
    height: 100vh;
    overflow: hidden;
    padding-top: 7rem;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/* CTA */

.cta{
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}

/* Header Socials */

.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap:0.8rem;
    gap:0.8rem;
    position: absolute;
    left : 0;
    bottom: 3rem;

}
.header__socials::after{
    content : '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);

}


/* ME */
.me{
    background: linear-gradient(var(--color-primary), transparent);
    width: 21rem;
    height: 21rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 12rem 12rem;
    overflow: hidden;
}



/* Scroll Down */
.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}


/* <--Tablets--> */
@media  screen and (max-width: 1024px) {
    header{
        height: 80vh;
        bottom:0;
        overflow:scroll;
    }
}

/* <--Mobiles--> */
@media  screen and (max-width: 600px) {

    header{
        height: 100vh;
    }
    .header__socials, .scroll__down{
        display:none;
    }
}


@media  screen and (max-width: 376px) {

    header{
        height: 120vh;
    }
}
nav{
    background: rgba(0,0,0,0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left:50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display:flex;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);

}
nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover{
    background: rgba(0,0,0,0.3);

}

nav a.active{
    background: var(--color-primary);
    color: var(--color-bg);
}
.about__container{
    display:grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}

.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        10deg,
        var(--color-primary),
        transparent
    );
    display: grid;
    place-items: center;
}
.about__me-image{
    border-radius: 2rem;
    -webkit-transform: rotate(-8deg);
            transform: rotate(-8deg);
    overflow: hidden;
    transition: var(--transition);
}

.about__me-image:hover{
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
.button_contact {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
}

/* <--Tablets--> */
@media  screen and (max-width: 1024px) {
    .about__container{
        grid-template-columns: 1fr;
        grid-gap:0;
        gap:0;

    }

    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about__content p{
        margin: 1rem 0 1.5rem;
    }
}


/* <--Mobiles--> */
@media  screen and (max-width: 600px) {
    
    .about__me{
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__cards{
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
    .about__content{
        text-align: center;
    }
    .about_content p{
        margin: 1.5rem 0;
    }
}
.experience__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem
}

.experience__container > div {
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience__container > div:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;

}

.experience__container > div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);

}

.experience__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}
.experience__details{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.experience__details-icon{
    margin-top: 6px;
    color: var(--color-primary);
}

/* <--Tablets--> */
@media  screen and (max-width: 1024px) {
    .experience__container {
        grid-template-columns: 1fr;

    }
    .experience__container > div{
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }

    .experience__content{
        padding: 1rem;
    }
}

/* <--Mobiles--> */
@media  screen and (max-width: 600px) {
    .experience__container{
        grid-gap: 1rem;
        gap: 1rem;
    }

    .experience__container > div{
        width: 100%;
        padding: 2rem 1rem;
    }
}


.logo1{
    height: 50px;
    width: 50px;
    border-radius: 50%;
    margin: auto;
}

.services__container{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 3rem;
    gap: 3rem;
}

.service{
    background: var(--color-bg-variant);
    border-radius: 0 0 2rem 2rem;
    border: 1px solid var(--color-primary);
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    transition: var(--transition);

}
.service:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;

}

.service__head{
    background: rgba(224, 224, 224, 0.519);
    padding: 2rem;
    border-radius: 0 0 2rem 2rem;
    box-shadow: 0 2rem 1 rem rgba(0, 0, 0, 0.1);
}

.service__head h3{
    color: var(--color-bg);
    font-size: 1rem;
    text-align: center;
}
.service__head h5{
    color: var(--color-bg);
    font-size: .8rem;
    text-align: center;
}

.service__list{
    padding: 2rem;
}

.service__list li{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 0.8rem;
}

.service__list-icon{
    color: var(--color-primary);
    margin-top : 2px;
}

.service__list p{
    font-size: 0.9rem;
}


/* <--Tablets--> */
@media  screen and (max-width: 1024px) {
    .service__container{
        grid-template-columns: 1fr 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
    .service{
        height: auto;
    }
}

/* <--Mobiles--> */
@media  screen and (max-width: 600px) {
    .service__container{
        grid-template-columns: 1fr;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }
}

.portfolio__container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
}

.portfolio__item{
    background: var(--color-bg-variant);
    padding: 1.3rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.portfolio__item:hover{
    border-color: var(--color-primary-variant);
}

.portfolio__item-image{
    height:auto;
    width:auto;
    border-radius: 1.5rem;
    overflow: hidden;
}

.portfolio__item h3{
    text-align: center;
    margin: 1.2rem 0 2rem;
}
.portfolio__item p{
    text-align: center;
    border-radius: 1.5rem;

}
.portfolio__item-cta{
    display: flex;
    justify-content: center;
    margin-bottom: 1rem;;
}


/* <--Tablets--> */
@media  screen and (max-width: 1024px) {
    .portfolio__container{
        grid-template-columns: 1fr 1fr;
        grid-gap: 1.2rem;
        gap: 1.2rem;
    }
}


/* <--Mobiles--> */
@media  screen and (max-width: 600px) {
    .portfolio__container{
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
}

.container.contact__container {
    width: 58;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;
}

.contact__options {
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

.contact_option {
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
}

.container__option:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
}

.contact__option-icon {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.contact__option a{
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}

/* FORM */

form{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

input, textarea{
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var(--color-white);
}


/* <--Tablets--> */
@media  screen and (max-width: 1024px) {
    .container.contact__container{
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
}


/* <--Mobiles--> */
@media  screen and (max-width: 600px) {
    .container.contact__container{
        width: var(--container-width-sm);   
    }
}
footer{
    background: var(--color-bg-variant);
    padding: 25px;
    bottom: 0;
    height: 310px;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 5rem;
}

footer a{
    color: var(--color-primary);

}

.footer__logo{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer__socials{
    display: flex;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer__socials a{
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}

.footer__socials a:hover{
    background: transparent;
    color: var(--color-primary);
    border-color: var(--color-bg);
}

.footer__copyright{
    margin-bottom: 4rem;
    color: var(--color-primary);
}

@media  screen and (max-width: 600px) {
    footer{
        height: auto;
    }
    .permalinks{
        flex-direction: column;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }
    .footer__socials{
        margin-bottom: 2.6rem;

    }
}
